import { api } from '../config/api';
import { z } from 'zod';

// Validation schemas
const accountSchema = z.object({
  id: z.string(),
  alias: z.string(),
  type: z.string(),
  value: z.number(),
  percentage: z.number(),
  performance: z.object({
    total: z.number(),
    percentage: z.number(),
    period: z.string()
  })
});

export type Account = z.infer<typeof accountSchema>;

const portfolioSchema = z.object({
  id: z.string(),
  name: z.string(),
  type: z.string(),
  allocation: z.array(z.object({
    symbol: z.string(),
    name: z.string(),
    percentage: z.number(),
    color: z.string()
  }))
});

export type Portfolio = z.infer<typeof portfolioSchema>;

export class AccountsService {
  static async getAccounts() {
    const response = await api.get('/accounts');
    return z.array(accountSchema).parse(response.data);
  }

  static async getAccount(id: string) {
    const response = await api.get(`/accounts/${id}`);
    return accountSchema.parse(response.data);
  }

  static async getAccountPerformance(id: string, period: string) {
    const response = await api.get(`/accounts/${id}/performance`, {
      params: { period }
    });
    return response.data;
  }

  static async getPortfolio(id: string) {
    const response = await api.get(`/accounts/${id}/portfolio`);
    return portfolioSchema.parse(response.data);
  }

  static async updatePortfolio(accountId: string, portfolioId: string) {
    const response = await api.put(`/accounts/${accountId}/portfolio`, {
      portfolioId
    });
    return portfolioSchema.parse(response.data);
  }
}