import React from 'react';
import { createRoot } from 'react-dom/client';
import { ThemeProvider } from './contexts/ThemeContext';
import { QueryProvider } from './contexts/QueryProvider';
import { WidgetAuthProvider } from './contexts/WidgetAuthContext';
import Dashboard from './pages/Dashboard';
import AccountDetail from './pages/AccountDetail';
import './styles/index.css';

interface WidgetConfig {
  containerId: string;
  token?: string;
  theme?: 'light' | 'dark';
  language?: 'en' | 'es' | 'pt';
  mode?: 'standalone' | 'embedded';
  view?: 'dashboard' | 'account';
  accountId?: string;
  enabledSections?: string[];
}

class VestivaWidget {
  private container: HTMLElement | null = null;
  private root: ReturnType<typeof createRoot> | null = null;
  private shadowRoot: ShadowRoot | null = null;

  constructor(private config: WidgetConfig) {
    this.init();
  }

  private init() {
    try {
      const { containerId } = this.config;
      this.container = document.getElementById(containerId);

      if (!this.container) {
        throw new Error(`Container with id "${containerId}" not found`);
      }

      // Create shadow DOM for style isolation
      this.shadowRoot = this.container.attachShadow({ mode: 'open' });
      const mountPoint = document.createElement('div');
      mountPoint.className = 'vestiva-widget';
      this.shadowRoot.appendChild(mountPoint);

      // Initialize React
      this.root = createRoot(mountPoint);
      this.render();

      // Dispatch ready event
      window.dispatchEvent(new CustomEvent('vestiva:ready', {
        detail: { containerId }
      }));
    } catch (error) {
      console.error('Error initializing widget:', error);
      window.dispatchEvent(new CustomEvent('vestiva:error', {
        detail: { error }
      }));
      throw error;
    }
  }

  private render() {
    if (!this.root) return;

    const { 
      mode = 'standalone',
      view = 'dashboard',
      accountId,
      token,
      theme,
      language,
      enabledSections 
    } = this.config;

    try {
      this.root.render(
        <React.StrictMode>
          <QueryProvider>
            <ThemeProvider initialTheme={theme}>
              <WidgetAuthProvider 
                token={token}
                domain={window.location.origin}
              >
                {view === 'dashboard' ? (
                  <Dashboard 
                    embedded={mode === 'embedded'} 
                    enabledSections={enabledSections}
                  />
                ) : (
                  <AccountDetail 
                    embedded={mode === 'embedded'} 
                    accountId={accountId}
                    enabledSections={enabledSections}
                  />
                )}
              </WidgetAuthProvider>
            </ThemeProvider>
          </QueryProvider>
        </React.StrictMode>
      );
    } catch (error) {
      console.error('Error rendering widget:', error);
      window.dispatchEvent(new CustomEvent('vestiva:error', {
        detail: { error }
      }));
      throw error;
    }
  }

  public destroy() {
    if (this.root) {
      this.root.unmount();
      this.root = null;
    }
  }
}

// Add type declaration for window object
declare global {
  interface Window {
    VestivaWidget: typeof VestivaWidget;
  }
}

// Export for IIFE bundle
if (typeof window !== 'undefined') {
  window.VestivaWidget = VestivaWidget;
}

// Export for module usage
export { VestivaWidget };
export type { WidgetConfig };